.alert{
    padding: 0.75rem 1.25rem;
    color: var(--mainWhite);
    text-align: center;
    margin:2rem auto 0 auto;
    border-radius: 2px;
    
}

.alert-danger{
    background-color: var(--mainred);    
}

.alert-success{
    background-color: var(--mainGreen);
}

.alert-delete{
    background-color: var(--mainblue);
}